import * as React from 'react';
import { variables, pick, SplunkThemeProvider } from '@splunk/themes';
import { Story } from '@storybook/react';
import styled from 'styled-components';

interface StorySizeDecoratorProps {
    width?: number;
    height?: number;
    backgroundColor?: string;
}

const Wrapper = styled.div<StorySizeDecoratorProps>`
    width: ${(props) => props.width || 360}px;
    height: ${(props) => props.height || 100}px;
    background-color: ${(props) =>
        props.backgroundColor
            ? props.backgroundColor
            : pick({
                  // TODO: use dashboard-themes variables
                  enterprise: {
                      light: variables.backgroundColor,
                      dark: variables.gray25, // gray20
                  },
                  prisma: {
                      light: variables.backgroundColorPage,
                      dark: '#15161b', // backgroundColorPage,
                  },
              })};
`;

export const withStorySize = (
    StoryTest: Story,
    context: { parameters: { storySize: StorySizeDecoratorProps } }
): JSX.Element => (
    <Wrapper
        height={context?.parameters?.storySize?.height}
        width={context?.parameters?.storySize?.width}
        backgroundColor={context?.parameters?.storySize?.backgroundColor}
    >
        <StoryTest />
    </Wrapper>
);

interface ThemeDecoratorProps {
    colorScheme?: 'light' | 'dark';
    family?: 'enterprise' | 'prisma';
    density?: 'comfortable' | 'compact';
}

export const withThemeProvider = (
    StoryTest: Story,
    context: { parameters?: { theme?: ThemeDecoratorProps } }
): JSX.Element => {
    return (
        <SplunkThemeProvider
            colorScheme={context?.parameters?.theme?.colorScheme ?? 'light'}
            density={context?.parameters?.theme?.density ?? 'comfortable'}
            family={context?.parameters?.theme?.family ?? 'enterprise'}
        >
            <StoryTest />
        </SplunkThemeProvider>
    );
};
